.companyMemberDropdown {
  background-color: white;
  border: 1px solid #707070;
  border-radius: 4px;
  font-size: 14px;
}

[dark-theme] {
  .companyMemberDropdown {
    background-color: #242b4384;
    border: none;
  }
}

.opener {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 38px;
  cursor: pointer;
  & > div {
    &:nth-child(1) {
      padding-left: 16px;
    }
    &:nth-child(2) {
      width: 38px;
      display: flex;
      justify-content: center;
      align-items: center;
      & > * {
        width: 12px;
        transition: all 0.2s;
      }
    }
  }
}

.popup {
  padding: 0 16px 16px;
  & > div:not(:last-child) {
    margin-bottom: 16px;
  }
}

.divider {
  border-top: 1px solid #707070;
  opacity: 0.5;
}

.list {
  margin: 0;
  line-height: 20px;
}

.buttonsWrapper {
  display: flex;
}

.button {
  width: 96px;
  height: 32px;
  text-transform: uppercase;
  border-width: 1px;
  border-style: solid;
  border-radius: 4px;
}
