.company {
  padding: 24px;
}

@media (min-width: 992px) {
  .company {
    padding: 40px;
  }
}

.title {
  margin-bottom: 24px;
  font-size: 24px;
  text-transform: uppercase;
}

.content {
  display: grid;
  grid-gap: 40px;
  grid-template:
    "section1" auto
    "section2" auto
    "section3 " auto / 100%;
  justify-content: center;
}

@media (min-width: 768px) {
  .content {
    grid-template:
      "section1 section3" max-content
      "section2 section3" auto / minmax(0, 312px) minmax(0, 312px);
  }
}

@media (min-width: 1200px) {
  .content {
    grid-template:
      "section1 section2 section3" auto / minmax(0, 312px) minmax(0, 312px)
      minmax(0, 312px);
  }
}

.section {
  &:nth-child(1) {
    grid-area: section1;
  }
  &:nth-child(2) {
    grid-area: section2;
  }
  &:nth-child(3) {
    grid-area: section3;
  }
}

.inputWrapper {
  &:not(:last-child) {
    margin-bottom: 24px;
  }
}

.label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 24px;
  line-height: 24px;
  font-size: 12px;
  & > *:last-child {
    font-size: 10px;
  }
}

.button {
  margin-top: 16px;
  width: 100%;
  height: 40px;
  font-size: 16px;
  color: #f7f7f7;
  background-color: #41b167;
  border-radius: 4px;
}

.companyMemberDropdown {
  &:not(:last-child) {
    margin-bottom: 16px;
  }
}
